<router-link class="feedback" :to="`/detail/`+feedback?._id"> <div class="upvote"><button v-on:click.self="upvote($event)"><img src="@/assets/navigate-up-arrow.png">{{feedback?.vote_count}}44</button></div><div class="content"><h2>{{feedback?.TITLE}}</h2><p>{{feedback?.DETAIL}}</p><div class="category">{{feedback?.CATEGORY}}</div></div><div class="comments_count"><img src="@/assets/speech-bubble.png">{{feedback?.COMMENTS?.length}}</div></router-link>